import React from "react";
import Menu from "./Menu";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import Rutine from "./Rutine";
import DayRutine from "./DayRutine";
import Exercise from "./Exercise";
import "../styles/main.scss";
import Login from "./Login";
import Signup from "./Signup";
import UserProfile from "./UserProfile";
import Home from "./Home";

const Main = () => {
  if (localStorage.getItem("user_token") == null) {
    return <Redirect to="/login" />;
  } else {
    return (
      <BrowserRouter>
        <div className="contenedor">
          <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/rutina" component={Rutine} />
            <Route path="/rutina-dia" component={DayRutine} />
            <Route path="/ejercicio" component={Exercise} />
            <Route path="/login" component={Login} />
            <Route path="/registrarse" component={Signup} />
            <Route path="/perfil" component={UserProfile} />
          </Switch>
        </div>
      </BrowserRouter>
    );
  }
};

export default Main;
