import React, { useState } from 'react';
import { MDBInput, MDBBtn } from "mdbreact";
import { Link, Redirect } from 'react-router-dom';
import Swal from 'sweetalert2';
import axios from 'axios';
import Loader from 'react-loader-spinner';

const Login = () => {

    const API_KEY = '1f76ae49-7962-4113-94e8-1ba316485aa1';

    const [credentials, setValues] = useState({
        username: '',
        password: '',
    });
    const [loggedIn, setLoggedIn] = useState(false);
    const [loading, setLoading] = useState(false);

    const handleInput = e => {
        setValues({
            ...credentials,
            [e.target.name]: e.target.value
        });
    }

    const authenticate = async () => {
        setLoading(true);

        const headers = {
            'api-key': API_KEY,
        };

        try {
            const res = await axios.post(`https://tna-backend.herokuapp.com/api/auth/get-api-token/`, { ...credentials }, { headers })
            const token = res.data.token;
            localStorage.setItem("user_token", token);
            setLoggedIn(true);
        } catch (e) {
            const error = e.response.data;
            const message = [Object.keys(error)[0]] + ': ' + error[Object.keys(error)[0]]
            errorPopup(message);
            setLoading(false);
        }

    }

    const errorPopup = message => {
        Swal.fire({
            icon: 'error',
            title: 'Oops!...',
            text: message
        })
    }

    return (
        loggedIn ? <Redirect to="/" /> :
            (
                <div>
                    <form>
                        <p className="h4 text-center py-4 grey-text">Iniciar sesión</p>
                        <div className="grey-text">
                            <MDBInput
                                style={{ color: 'white' }}
                                label="Email"
                                icon="envelope"
                                group
                                type="email"
                                validate
                                error="wrong"
                                success="right"
                                name="username"
                                value={credentials.username}
                                onInput={handleInput}
                            />
                            <MDBInput
                                style={{ color: 'white' }}
                                label="Contraseña"
                                icon="key"
                                group
                                type="password"
                                validate
                                error="wrong"
                                success="right"
                                name="password"
                                value={credentials.password}
                                onInput={handleInput}
                            />
                        </div>
                        <div className="text-center py-4 mt-3">
                            <Link to='/registrarse'><MDBBtn color="primary">Registrarse</MDBBtn></Link>
                            <MDBBtn color="success" onClick={!loading ? authenticate : null}>
                                {loading ? (
                                    <Loader
                                        type="ThreeDots"
                                        color="white"
                                        height={16}
                                        width={75}
                                    />
                                ) : ('Ingresar')}
                            </MDBBtn>
                        </div>
                    </form>

                </div>
            )
    );
}

export default Login;
