import React, {useEffect,useState} from 'react';
import Day from './Day';
import { connect } from 'react-redux';
import axios from 'axios';
import Swal from 'sweetalert2';

const Rutine = (props) => {
    const { rutine } = props;
    const API_KEY = '1f76ae49-7962-4113-94e8-1ba316485aa1';
    const URL = 'https://tna-backend.herokuapp.com/api';
    const token = localStorage.getItem('user_token');
    const headers = {
        'api-key': API_KEY,
        'Authorization': "bearer " + token
    };      

    const [loading, setLoading] = useState(false);
    
    const getUserRoutine = async () =>{
        setLoading(true);
        try{
            const res = await axios.get(`${URL}/training/routines/`, { headers });
            setLoading(false);
            console.log(res.data);
        }catch (e){
            const error = e.response.data;
            const message = error[Object.keys(error)[0]];
            errorPopup(message);
            setLoading(false);
        }
    }

    const errorPopup = message => {
        Swal.fire({
            icon: 'error',
            title: 'Oops!...',
            text: message
        })
    }

    useEffect(() => {
        getUserRoutine();
    }, [])
    
    return (
        <div>
            {rutine.days.map(function (day, idx) {
                return (
                    <Day key={idx} day={day.name} imageURL={day.image} />
                )
            })}
        </div>
    )
}

const mapStateToProps = state => ({
    rutine: state.rutine
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(Rutine);
