import React from 'react';
import '../styles/day.scss';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
// import { setDay } from '../actions/actions';
import { fetchData } from '../actions/actions';

const Day = (props) => {

    const routeChange = () => {
        props.history.push('/rutina-dia');
    }

    const { day, imageURL, dayChange } = props;
    return (
        <div onClick={() => { routeChange(); dayChange(day) }} className="day-container" style={{ backgroundImage: `url(${imageURL})` }}>
            <div className="day">
                {day}
            </div>
        </div>
    )
}

const mapStateToProps = state => ({})

const mapDispatchToProps = dispatch => ({
    dayChange: day => dispatch(fetchData(day))
})

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Day));
