import React from 'react';
import { connect } from 'react-redux';
import '../styles/dayRutine.scss';
import biceps from '../img/exercises/biceps.jpg';
import triceps from '../img/exercises/triceps.jpg';
import shoulders from '../img/exercises/shoulder.jpg';
import Exercise from './Exercise';

const DayRutine = (props) => {
    const { day } = props;
    let days = [
        {
            name: "Lunes",
            exercises: [
                {
                    name: 'Bíceps',
                    description: 'con mancuernas de 5kg',
                    series: '4',
                    repetitions: '10',
                    image: biceps
                },
                {
                    name: 'Tríceps',
                    description: 'con polea',
                    series: '4',
                    repetitions: '15',
                    image: triceps
                },
                {
                    name: 'Hombros',
                    description: 'con máquina',
                    series: '4',
                    repetitions: '10',
                    image: shoulders
                }
            ]
        }
    ]

    // const routeChange = () => {
    //     props.history.push('/ejercicio');
    // }

    return (
        <div className="day-rutine">
            <div className="day">{day}</div>
            {days[0].exercises.map(function (exercise, idx) {
                return (
                    <div /*onClick={() => { routeChange(); }}*/ key={idx} className="exercise">
                        <span style={{fontSize: '25px'}}>{exercise.name}</span>   
                        <img className="exercise-image" src={exercise.image} alt={exercise.name} />
                        <Exercise />                     
                    </div>                    
                )
            })}
        </div>
    )
}

const mapStateToProps = state => ({
    day: state.day
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(DayRutine);
