export const SET_DAY = 'SET_DAY';

export const setDay = payload => {
    return{
        type: SET_DAY,
        payload
    }
}

export const fetchData = payload => {
    return dispatch => {
        const url = 'http://localhost:3000/users/1';
        return fetch(url).then(
            data => (data.json())
        ).then(
            data => {
                dispatch(setDay(data.username))
            }
        )
    }
}