import React, { useState } from 'react';
import { MDBInput, MDBBtn } from "mdbreact";
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import axios from 'axios';
import Loader from 'react-loader-spinner';

const Signup = () => {

    const API_KEY = '1f76ae49-7962-4113-94e8-1ba316485aa1';

    const [userData, setValues] = useState({
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        mobile_phone: '',
        birth_date: ''
    });
    const [loading, setLoading] = useState(false);

    const handleInput = e => {
        setValues({
            ...userData,
            [e.target.name]: e.target.value
        });
    }

    const register = async () => {
        setLoading(true);
        const headers = {
            'api-key': API_KEY,
        };

        try {
            await axios.post(`https://tna-backend.herokuapp.com/api/patients/register`, { ...userData }, { headers });
            const message = '¡Registro exitoso!';
            successPopup(message);
        } catch (e) {
            const error = e.response.data;
            const message = [Object.keys(error)[0]] + ': ' + error[Object.keys(error)[0]]
            errorPopup(message);
            setLoading(false);
        }
    }

    const errorPopup = message => {
        Swal.fire({
            icon: 'error',
            title: 'Oops!...',
            text: message
        })
    }

    const successPopup = message => {
        const Toast = Swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000
        })
        Toast.fire({
            icon: 'success',
            title: message
        })
    }

    return (
        <form>
            <p className="h4 text-center py-4 grey-text">Registro</p>
            <div className="grey-text">
                <MDBInput
                    style={{ color: 'white' }}
                    label="Nombre"
                    icon="file-signature"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    name="first_name"
                    value={userData.first_name}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Apellido"
                    icon="file-signature"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    name="last_name"
                    value={userData.last_name}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Email"
                    icon="envelope"
                    group
                    type="email"
                    validate
                    error="wrong"
                    success="right"
                    name="email"
                    value={userData.email}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Contraseña"
                    icon="lock"
                    group
                    type="password"
                    validate
                    error="wrong"
                    success="right"
                    name="password"
                    value={userData.password}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Celular"
                    icon="mobile-alt"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="mobile_phone"
                    value={userData.mobile_phone}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Fecha de nacimiento"
                    icon="birthday-cake"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    name="birth_date"
                    value={userData.birth_date}
                    onInput={handleInput}
                />
            </div>
            <div className="text-center py-4 mt-3">
                <Link to='/login'><MDBBtn color="deep-orange">Atrás</MDBBtn></Link>
                <MDBBtn color="primary" onClick={!loading ? register : null}>
                    {loading ? (
                        <Loader
                            type="ThreeDots"
                            color="white"
                            height={16}
                            width={75}
                        />
                    ) : ('Registrarse')}
                </MDBBtn>
            </div>
        </form>
    );
}

export default Signup;
