import React, { useState } from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Gym from '../img/svgs/Gym';
import Diet from '../img/svgs/Diet';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import { Link, Redirect } from 'react-router-dom';
import Recipes from '../img/svgs/Recipes';
import Logout from '../img/svgs/Logout';
import profilePicture from '../img/png/profile-picture.png';
import tna from '../img/TNA.png';

const drawerWidth = 200;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    backgroundColor: '#171717',
    zIndex: 1000
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor: "#f7f7f7"
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  iconsWith: {
    minWidth: "45px"
  },
  tnaIcon: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    marginRight: "1rem"
  }
}));

export default function PersistentDrawerLeft() {


  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = useState(false);
  const [loggedOut, setLoggedOut] = useState(false);

  function handleDrawerOpen() {
    setOpen(true);
  }

  function handleDrawerClose() {
    setOpen(false);
  }

  const logout = () => {
    localStorage.clear();
    setLoggedOut(true);
  }

  return (
    loggedOut ? <Redirect to="/login" /> :
      (
        <ClickAwayListener onClickAway={handleDrawerClose}>
          <div className={classes.root}>
            <CssBaseline />
            <AppBar
              position="fixed"
              className={clsx(classes.appBar, {
                [classes.appBarShift]: open,
              })}
            >
              <Toolbar>
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  onClick={handleDrawerOpen}
                  edge="start"
                  className={clsx(classes.menuButton, open && classes.hide)}
                >
                  <MenuIcon />
                </IconButton>
                <div className={classes.tnaIcon}>
                  <img src={tna} alt="TNA" />
                </div>
              </Toolbar>
            </AppBar>
            <Drawer
              className={classes.drawer}
              variant="persistent"
              anchor="left"
              open={open}
              classes={{
                paper: classes.drawerPaper,
              }}
            >
              <div className={classes.drawerHeader}>
                <IconButton onClick={handleDrawerClose}>
                  {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                </IconButton>
              </div>
              <Divider />
              <List>
                <ListItem onClick={handleDrawerClose} button key={'myProfile'} component={Link} to={"/perfil"}>
                  <ListItemIcon className={classes.iconsWith}><img style={{ width: "32px", height: "32px" }} src={profilePicture} alt="profilePicture" /></ListItemIcon>
                  <ListItemText primary={'Mi perfil'} />
                </ListItem>
                <ListItem onClick={handleDrawerClose} button key={'rutine'} component={Link} to={"/rutina"}>
                  <ListItemIcon className={classes.iconsWith}><Gym style={{ width: "30px", height: "30px" }} /></ListItemIcon>
                  <ListItemText primary={'Rutina'} />
                </ListItem>
                <ListItem onClick={handleDrawerClose} button key={'diet'} component={Link} to={"#"}>
                  <ListItemIcon className={classes.iconsWith}><Diet style={{ width: "32px", height: "32px" }} /></ListItemIcon>
                  <ListItemText primary={'Dieta'} />
                </ListItem>
                <ListItem onClick={handleDrawerClose} button key={'recipes'} component={Link} to={"#"}>
                  <ListItemIcon className={classes.iconsWith}><Recipes style={{ width: "32px", height: "32px" }} /></ListItemIcon>
                  <ListItemText primary={'Recetas'} />
                </ListItem>
              </List>
              <Divider />
              <List>
                <ListItem button key={'logout'} onClick={logout}>
                  <ListItemIcon className={classes.iconsWith}><Logout style={{ width: "32px", height: "32px" }}/></ListItemIcon>
                  <ListItemText primary={'Cerrar sesión'} />
                </ListItem>
              </List>
            </Drawer>
          </div>
        </ClickAwayListener>
      )
  );
}
