import React from "react";
import tna from "../img/TNA.png";
import workout from "../img/home/workout.jpg";
import nutrition from "../img/home/nutrition.jpg";
import recipies from "../img/home/recipies.jpeg";
import promos from "../img/home/promos.jpg";

const Home = () => {
  return (
    <div
      style={{ display: "flex", flexDirection: "column", alignItems: "center" }}
    >
      <img style={{ marginBottom: "16px" }} src={tna} alt="TNA" />
      <a
        style={{
          width: "100%",
          height: "120px",
          backgroundColor: "white",
          margin: "8px 0",
          display: "flex",
          alignItems: "flex-end",
          backgroundImage: `url(${workout})`,
          backgroundSize: "cover",
          color: "black"
        }}
        href="/rutina"
      >
        <span style={{ padding: "0 0 8px 16px" }}>Plan de entrenamiento</span>
      </a>
      <div
        style={{
          width: "100%",
          height: "120px",
          backgroundColor: "white",
          margin: "8px 0",
          display: "flex",
          alignItems: "flex-end",
          backgroundImage: `url(${nutrition})`,
          backgroundSize: "cover",
          color: "black"
        }}
        href="#"
      >
        <span style={{ padding: "0 0 8px 16px" }}>Plan de alimentación</span>
      </div>
      <div
        style={{
          width: "100%",
          height: "120px",
          backgroundColor: "white",
          margin: "8px 0",
          display: "flex",
          alignItems: "flex-end",
          backgroundImage: `url(${recipies})`,
          backgroundSize: "cover",
          color: "black"
        }}
        href="#"
      >
        <span style={{ padding: "0 0 8px 16px" }}>Recetario</span>
      </div>
      <div
        style={{
          width: "100%",
          height: "120px",
          backgroundColor: "white",
          margin: "8px 0",
          display: "flex",
          alignItems: "flex-end",
          backgroundImage: `url(${promos})`,
          backgroundSize: "cover",
          color: "black"
        }}
        href="#"
      >
        <span
          style={{ padding: "0 0 8px 16px" }}
        >{`Promociones & Beneficios`}</span>
      </div>
    </div>
  );
};

export default Home;
