import React from 'react';
import { connect } from 'react-redux';
import YouTube from 'react-youtube';
import '../styles/exercise.scss';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const Exercise = (props) => {
    const { rutine } = props;

    const opts = {
        height: '220px',
        width: '100%',
    };

    const useStyles = makeStyles(theme => ({
        root: {
            width: '100%',
        },
        heading: {
            fontSize: theme.typography.pxToRem(15),
            flexBasis: '33.33%',
            flexShrink: 0,
        },
        secondaryHeading: {
            fontSize: theme.typography.pxToRem(15),
            color: theme.palette.text.secondary,
        },
    }));

    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = panel => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    return (
        <div>
            {rutine.days[0].exercises.map(function (exercise, idx) {
                return (
                    <div key={idx} className={classes.root}>
                        <ExpansionPanel expanded={expanded === idx} onChange={handleChange(idx)}>
                            <ExpansionPanelSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls={`${idx}bh-content`}
                                id={`${idx}bh-header`}
                            >
                                <div style={{fontSize: '20px'}}>{exercise.name}</div>
                            </ExpansionPanelSummary>
                            <ExpansionPanelDetails>
                                <div className="individual-exercise">                                  
                                    <div className="exercise-series-repetitions">
                                        <div>Series: {exercise.series}</div>
                                        <div>Repeticiones: {exercise.repetitions}</div>                                        
                                    </div>
                                    <div className="exercise-series-repetitions">
                                        <div>Técnica: {exercise.technique}</div>
                                        <div>Descanso: {exercise.rest}</div>
                                    </div>
                                    <div style={{marginTop: '0.25rem', fontSize: '18px', display: 'flex', justifyContent: 'center'}}>
                                        RPE: {exercise.rpe}</div>                                 
                                    <div className="title">Notas:</div>
                                    <div style={{marginBottom: '0.5rem'}} className="text">{exercise.notes}</div>
                                    <YouTube                                        
                                        videoId={exercise.video}
                                        opts={opts}
                                    />
                                </div>
                            </ExpansionPanelDetails>
                        </ExpansionPanel>
                    </div>
                )
            })}
        </div>
    );
}

const mapStateToProps = state => ({
    rutine: state.rutine
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(Exercise);